import { createViewportAwareInputHandler } from "gdxjs";
import createWorld from "../createWorld";
import createBackGroundRenderSystem from "../system/BgRenderSystem";
import createRenderArrowButtonSystem from "../system/ArrowButtonRenderSystem";
import createInputHandleGameScreenSystem from "../system/InputHandlerGameScreenSystem";
import createButtonRenderSystem from "../system/ButtonRenderSystem";
import createDurationRenderSystem from "../system/BarDurationRenderSystem";
import createHandAnimationRenderSystem from "../system/HandAnimationRenderSystem";
import createCurtainRenderSystem from "../system/CurtainRenderSystem";
import createCaroRenderSystem from "../system/CaroBgRenderSystem";
import data from "../data";

import createRenderEndScreenSystem from "./EndScreen";

export default (Game) => {
  const {
    canvas,
    width,
    height,
    gl,
    batch,
    whiteTex,
    camera,
    pixelRatio,
    assets,
    spriter,
    viewport,
    sound,
  } = Game;

  // const inputHandler = new InputHandler(canvas);
  const inputHandler = createViewportAwareInputHandler(canvas, viewport);

  const datas = data;
  const lines = datas.split("\n");
  const infos = [];
  for (let i = 0; i < lines.length; i++) {
    const value = lines[i].split(",").map((s) => s.trim());
    infos.push({
      start: parseFloat(value[0]),
      duration: parseFloat(value[1]),
      nodes: parseFloat(value[2]),
      dancingDuration: parseFloat(value[3]),
      anim: value[4],
    });
  }

  let intro = infos[0];
  infos.shift();

  sound.play();

  const scripts = [];
  for (let i = 0; i < infos.length; i++) {
    scripts.push(infos[i].nodes);
  }

  const notes = []; // cha type, opacity mi nt / phase

  for (let s of scripts) {
    const tmpArray = [];
    for (let i = 0; i < s; i++) {
      tmpArray.push({
        type: Math.floor(Math.random() * 4),
        activated: false,
      });
    }
    notes.push(tmpArray);
  }

  // console.log(notes);
  //  note information
  const NOTE_SIZE = width / 7;
  const NOTE_MARGIN = width / 100;
  const NOTE_START_Y = height / 2 + height / 30;
  const NOTE_RECTS = [];

  for (let i = 0; i < notes.length; i++) {
    const tmpArray = [];
    let NOTE_Y = 0;
    let NOTE_X = 0;
    let length = 0;
    for (let j = 0; j < notes[i].length; j++) {
      let noteI = 0;
      if (j <= 3) {
        length = notes[i].length;
        NOTE_X =
          (width -
            NOTE_MARGIN * (Math.min(4, length) - 1) -
            NOTE_SIZE * Math.min(4, length)) /
          2;
        NOTE_Y = NOTE_START_Y;
        noteI = j;
      } else {
        length = notes[i].length - 4;
        NOTE_X = (width - NOTE_MARGIN * (length - 1) - NOTE_SIZE * length) / 2;
        NOTE_Y = NOTE_START_Y + height / 14;
        noteI = Math.abs(4 - j);
      }

      tmpArray.push({
        x: NOTE_X + (NOTE_SIZE + NOTE_MARGIN) * noteI,
        y: NOTE_Y,
        width: NOTE_SIZE,
        height: NOTE_SIZE,
      });
    }
    NOTE_RECTS.push(tmpArray);
  }

  const missAnim = "bam_sai";

  const worldState = {
    runIntro: false,
    hit: 0,
    miss: 0,
    anim: "",
    missAnim: missAnim,
    animIndex: -1,
    stateTime: 0,
    keyActivated: [],
    boundaryY: height / 2 - height / 20,
    gameOver: false,
    buttonInfos: null,
    durationBarInfos: {
      duration: 4,
      x: (width - (width * 4) / 5) / 2,
      y: (height * 3) / 4,
      width: (width * 4) / 5,
      height: height / 10,
    },
    notes: notes,
    noteInfos: NOTE_RECTS,
    currentCheckingNoteIndex: 0,
    phaseIndex: -1,
    durationEachPhase: 4,
    realTimeDurationSet: 4,
    hitRight: false,
    hitMiss: false,
    canPassPhase: false, // check when mark in metter
    finishedPhase: false, // check when all note finished
    zoomOutNoteIndexs: [],
    zoomOutNoteInfos: [],
    hitFinishButton: false,
    starEffect: { scale1: 1, opacity1: 1, scale2: 1, opacity2: 1 },
    finishMarkEffect: { scale: 1, opacity: 1 },
    textPopUp: { opacity: 1, scale: 0.5, startY: (height * 1.3) / 3 },
  };

  const extra = {
    canvas,
    width,
    height,
    gl,
    batch,
    whiteTex,
    camera,
    pixelRatio,
    assets,
    spriter,
    inputHandler,
  };

  // let MISS = 0;
  // let HIT = 0;

  const world = createWorld(worldState, extra);
  world.addSystem(createCaroRenderSystem());
  world.addSystem(createHandAnimationRenderSystem());
  world.addSystem(createBackGroundRenderSystem());
  world.addSystem(createCurtainRenderSystem());
  world.addSystem(createRenderArrowButtonSystem());
  world.addSystem(createInputHandleGameScreenSystem());
  world.addSystem(createDurationRenderSystem());
  world.addSystem(createButtonRenderSystem());

  const goToEndScreen = async () => {
    Game.setCurrentScreen(await createRenderEndScreenSystem(Game));
  };

  let timeAccumulate = 0;
  let counter = -1;
  let animCounter = 0;
  let readyToChangePhase = true;
  let loadNewPhase = false;

  const update = (delta) => {
    timeAccumulate += delta;

    if (
      timeAccumulate > intro.start &&
      timeAccumulate <= intro.start + intro.duration
    ) {
      worldState.anim = missAnim;
      worldState.runIntro = true;
    } else {
      worldState.runIntro = false;

      if (!worldState.hitFinishButton) {
        worldState.stateTime += delta;
      }

      if (infos[counter + 1] !== undefined) {
        if (timeAccumulate > infos[counter + 1].start) {
          readyToChangePhase = true;
        }

        if (timeAccumulate > infos[counter + 1].start && readyToChangePhase) {
          readyToChangePhase = false;
          loadNewPhase = true;
          counter = Math.min((counter += 1), infos.length);
        }
      }

      // check end game
      if (
        timeAccumulate >
        infos[infos.length - 1].start + infos[infos.length - 1].duration
      ) {
        Game.completePercentage = Math.floor(
          (worldState.hit / infos.length) * 100
        );
        goToEndScreen();
      }

      // reset value
      if (timeAccumulate > infos[counter].start && loadNewPhase) {
        loadNewPhase = false;
        worldState.starEffect = {
          scale1: 1,
          scale2: 1,
          opacity1: 1,
          opacity2: 1,
        };
        worldState.textPopUp = {
          opacity: 1,
          scale: 0.5,
          startY: (height * 1.3) / 3,
        };
        worldState.hitFinishButton = false;
        worldState.hitRight = false;
        worldState.finishedPhase = false;
        worldState.canPassPhase = false;
        worldState.zoomOutNoteIndexs.length = 0;
        worldState.zoomOutNoteInfos.length = 0;
        worldState.durationEachPhase = infos[counter].duration;
        worldState.realTimeDurationSet =
          worldState.stateTime + worldState.durationEachPhase;
        worldState.currentCheckingNoteIndex = 0;
        worldState.phaseIndex += 1;
        worldState.phaseIndex =
          worldState.phaseIndex > scripts.length - 1
            ? scripts.length - 1
            : worldState.phaseIndex;
      }
      /// update anim
      if (
        timeAccumulate <
        infos[animCounter].start +
          infos[animCounter].duration +
          infos[animCounter].dancingDuration
      ) {
        if (worldState.hitMiss) {
          worldState.anim = missAnim;
        } else if (worldState.hitRight) {
          if (animCounter < counter) {
            animCounter = counter;
          }
          worldState.anim = infos[animCounter].anim;
        }
      } else {
        animCounter = Math.min((animCounter += 1), infos.length);
        worldState.hitMiss = false;
        worldState.anim = missAnim;
      }
    }
    batch.setProjection(camera.combined);
    world.update(delta);
  };

  return {
    update,
    destroy() {
      inputHandler.cleanup();
    },
  };
};
