import { fillRect } from "gdxjs";

const checkScoreType = (
  realTimeDurationSet,
  stateTime,
  durationEachPhase,
  durationZone,
  scoreType,
  textList,
  textListInfos
) => {
  let texture;
  let centerTime =
    durationEachPhase - (2.3 / 4) * durationEachPhase - durationZone / 2;
  let minTime = durationZone / 5;
  if (
    realTimeDurationSet - stateTime < centerTime + minTime / 2 &&
    realTimeDurationSet - stateTime > centerTime - minTime / 2
  ) {
    // console.log("perfect");
    scoreType = "perfect";
    texture = { tex: textList[0], infos: textListInfos[0] };
  } else if (
    (realTimeDurationSet - stateTime < centerTime - minTime / 2 &&
      realTimeDurationSet - stateTime > centerTime - (minTime * 3) / 2) ||
    (realTimeDurationSet - stateTime < centerTime + (minTime * 3) / 2 &&
      realTimeDurationSet - stateTime > centerTime + minTime / 2)
  ) {
    // console.log("great");
    scoreType = "great";
    texture = { tex: textList[1], infos: textListInfos[1] };
  } else if (
    (realTimeDurationSet - stateTime < centerTime - (minTime * 3) / 2 &&
      realTimeDurationSet - stateTime > centerTime - (minTime * 5) / 2) ||
    (realTimeDurationSet - stateTime < centerTime + (minTime * 5) / 2 &&
      realTimeDurationSet - stateTime > centerTime + (minTime * 3) / 2)
  ) {
    // console.log("cool");
    scoreType = "cool";
    texture = { tex: textList[2], infos: textListInfos[2] };
  } else {
    scoreType = "miss";
    texture = { tex: textList[3], infos: textListInfos[3] };
  }

  return texture;
  // return scoreType;
};

const createDurationRenderSystem = () => {
  let finishBarInfos = {};
  let finishMetterInfos = {};
  let finishMarkInfos = {};
  let finishBar, finishMark, finishMetter, markEffect;
  let durationZone;
  let BAR_WIDTH, METTER_WIDTH, MARK_WIDTH, ME_WIDTH, ME_HEIGHT;
  let scoreType;
  let cool, great, perfect, miss, textList;
  let coolInfos, greatInfos, perfectInfos, missInfos, textListInfos;
  return {
    init({ boundaryY }, { assets, width, height }) {
      const atlas = assets.auditionAtlas;
      const finishAtlas = assets.finishAtlas;

      finishBar = assets.finishBar;

      finishMetter = assets.mask;
      finishMark = atlas.findRegion("finish_mark", -1);
      markEffect = finishAtlas.findRegion("finsh_mark_effect", -1);

      cool = finishAtlas.findRegion("cool", -1);
      great = finishAtlas.findRegion("great", -1);
      perfect = finishAtlas.findRegion("perfect", -1);
      miss = finishAtlas.findRegion("miss", -1);

      textList = [perfect, great, cool, miss];
      const COOL_H = height / 25;
      const COOL_W = (COOL_H * cool.width) / cool.height;
      const GREAT_H = height / 25;
      const GREAT_W = (GREAT_H * great.width) / great.height;
      const PERFECT_H = height / 25;
      const PERFECT_W = (PERFECT_H * perfect.width) / perfect.height;
      const MISS_H = height / 25;
      const MISS_W = (MISS_H * miss.width) / miss.height;

      coolInfos = { width: COOL_W, height: COOL_H };
      greatInfos = { width: GREAT_W, height: GREAT_H };
      perfectInfos = { width: PERFECT_W, height: PERFECT_H };
      missInfos = { width: MISS_W, height: MISS_H };
      textListInfos = [perfectInfos, greatInfos, coolInfos, missInfos];

      BAR_WIDTH = (width * 8.6) / 9;
      const BAR_HEIGHT = (BAR_WIDTH * finishBar.height) / finishBar.width;
      const BAR_START_X = (width - BAR_WIDTH) / 2;

      MARK_WIDTH = BAR_WIDTH / 30;
      const MARK_HEIGHT = (MARK_WIDTH * finishMark.height) / finishMark.width;

      markEffect = finishAtlas.findRegion("finsh_mark_effect", -1);
      ME_WIDTH = MARK_WIDTH;
      ME_HEIGHT = (ME_WIDTH * markEffect.height) / markEffect.width;

      METTER_WIDTH = BAR_WIDTH / 4;
      const METTER_HEIGHT = (BAR_HEIGHT * 2.5) / 4;

      finishBarInfos = {
        x: BAR_START_X,
        y: boundaryY + height / 40,
        width: BAR_WIDTH,
        height: BAR_HEIGHT,
      };

      finishMetterInfos = {
        x: BAR_START_X + (2.3 / 4) * BAR_WIDTH,
        y: finishBarInfos.y + BAR_HEIGHT / 2 - METTER_HEIGHT / 2,
        width: METTER_WIDTH,
        height: METTER_HEIGHT,
      };

      finishMarkInfos = {
        y: finishBarInfos.y + BAR_HEIGHT / 2 - MARK_HEIGHT / 2,
        width: MARK_WIDTH,
        height: MARK_HEIGHT,
      };
    },
    update(delta, worldState, { width, height, batch, camera }) {
      let { stateTime, realTimeDurationSet, durationEachPhase } = worldState;
      finishMarkInfos.x =
        finishBarInfos.x +
        BAR_WIDTH -
        ((realTimeDurationSet - stateTime) / durationEachPhase) * BAR_WIDTH;

      durationZone = (METTER_WIDTH * durationEachPhase) / BAR_WIDTH;

      if (
        realTimeDurationSet - stateTime <
          durationEachPhase - (2.3 / 4) * durationEachPhase &&
        realTimeDurationSet - stateTime >
          durationEachPhase - (2.3 / 4) * durationEachPhase - durationZone &&
        worldState.finishedPhase
      ) {
        worldState.canPassPhase = true;
      } else {
        worldState.canPassPhase = false;
      }

      //render
      batch.setProjection(camera.combined);
      batch.begin();
      fillRect(
        batch,
        finishBar,
        finishBarInfos.x,
        finishBarInfos.y,
        finishBarInfos.width,
        finishBarInfos.height
      );

      // draw finish METTER
      batch.setColor(1, 1, 1, 0.5);
      fillRect(
        batch,
        finishMetter,
        finishMetterInfos.x,
        finishMetterInfos.y,
        finishMetterInfos.width,
        finishMetterInfos.height
      );
      batch.setColor(1, 1, 1, 0.75);
      fillRect(
        batch,
        finishMetter,
        finishMetterInfos.x + finishMetterInfos.width / 5,
        finishMetterInfos.y,
        (finishMetterInfos.width * 3) / 5,
        finishMetterInfos.height
      );
      batch.setColor(1, 1, 1, 1);
      fillRect(
        batch,
        finishMetter,
        finishMetterInfos.x + (finishMetterInfos.width * 2) / 5,
        finishMetterInfos.y,
        finishMetterInfos.width / 5,
        finishMetterInfos.height
      );

      // draw text
      if (worldState.hitFinishButton) {
        let texture = checkScoreType(
          realTimeDurationSet,
          stateTime,
          durationEachPhase,
          durationZone,
          scoreType,
          textList,
          textListInfos
        );
        worldState.textPopUp.startY = Math.max(
          (height * 1.1) / 3,
          (worldState.textPopUp.startY -= (delta * height) / 3)
        );

        // console.log(worldState.textPopUp.scale);
        worldState.textPopUp.scale = Math.min(
          1,
          (worldState.textPopUp.scale += delta * 5)
        );

        if (worldState.textPopUp.startY === (height * 1.1) / 3) {
          worldState.textPopUp.opacity = Math.max(
            0,
            (worldState.textPopUp.opacity -= delta)
          );
        }

        batch.setColor(1, 1, 1, worldState.textPopUp.opacity);
        texture.tex.draw(
          batch,
          width / 2 - texture.infos.width / 2,
          worldState.textPopUp.startY,
          texture.infos.width,
          texture.infos.height,
          texture.infos.width / 2,
          texture.infos.height / 2,
          0,
          worldState.textPopUp.scale,
          worldState.textPopUp.scale
        );
      }

      if (!worldState.runIntro) {
        if (!worldState.hitFinishButton) {
          if (finishMarkInfos.x <= BAR_WIDTH + finishMarkInfos.width / 2) {
            worldState.finishMarkEffect = { scale: 1, opacity: 1 };
            finishMark.draw(
              batch,
              finishMarkInfos.x - finishMarkInfos.width / 2,
              finishMarkInfos.y,
              finishMarkInfos.width,
              finishMarkInfos.height
            );
          }
        } else {
          // mark effect
          worldState.finishMarkEffect.scale = Math.min(
            2.3,
            (worldState.finishMarkEffect.scale += delta * 10)
          );
          if (worldState.finishMarkEffect.scale === 2.3) {
            worldState.finishMarkEffect.opacity = Math.max(
              0,
              (worldState.finishMarkEffect.opacity -= delta * 5)
            );
          }
          batch.setColor(1, 1, 1, worldState.finishMarkEffect.opacity);
          markEffect.draw(
            batch,
            finishMarkInfos.x +
              finishMarkInfos.width / 2 -
              ME_WIDTH / 2 -
              finishMarkInfos.width / 2,
            finishMarkInfos.y + finishMarkInfos.height / 2 - ME_HEIGHT / 2,
            ME_WIDTH,
            ME_HEIGHT,
            ME_WIDTH / 2,
            ME_HEIGHT / 2,
            0,
            worldState.finishMarkEffect.scale,
            worldState.finishMarkEffect.scale
          );
          batch.setColor(1, 1, 1, 1);
        }
      }
      batch.end();
    },
  };
};

export default createDurationRenderSystem;
